@keyframes vibrate {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translate(-2px, 2px);
  }

  40% {
    transform: translate(-2px, -2px);
  }

  60% {
    transform: translate(2px, 2px);
  }

  80% {
    transform: translate(2px, -2px);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes turnLeft {
  10% {
    opacity: 1;
    left: 15%;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    left: 0%;
  }
}

@keyframes turnLeft_animation {
  10% {
    opacity: 1;
    right: 25%;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    top: 70%;
    right: 50%;
    transform: scale(-.2, .2);
  }
}

@keyframes turnForward_animation {
  10% {
    opacity: .2;
    bottom: 21%;
    left: 26%;
    transform: scale(-.2, .2);
  }

  30% {
    opacity: .5;
    bottom: 17.5%;
    left: 24.75%;
    transform: scale(-.9, .9);
  }

  50% {
    opacity: .6;
    bottom: 15%;
    left: 24.5%;
    transform: scale(-1, 1);
  }

  100% {
    opacity: 1;
    bottom: 10%;
    left: 23%;
    transform: scale(-2.2, 2.2);
  }
}

