.switch {
  cursor: pointer;
  align-items: center;
  display: flex;
}

.switch:after {
  content: "";
  background-color: #fff;
  width: 3px;
  height: 315px;
  position: absolute;
  top: 0;
  right: -4%;
}

.switch .switch__inp {
  display: none;
}

.switch .switch__inp + span {
  white-space: nowrap;
  padding-left: 57px;
  transition: color .3s;
  display: block;
  position: relative;
}

.switch .switch__inp + span:before, .switch .switch__inp + span:after {
  content: "";
  border-radius: 12px;
  display: block;
  position: absolute;
}

.switch .switch__inp + span:before {
  background-color: #fff;
  border: 1px solid #fff;
  width: 42px;
  height: 20px;
  transition: all .3s;
  top: 0;
  left: 0;
}

.switch .switch__inp + span:after {
  background: #000;
  width: 16px;
  height: 16px;
  transition: all .45s;
  top: 2px;
  left: 3px;
  box-shadow: 0 1px 3px rgba(18, 22, 33, .1);
}

.switch .switch__inp:checked + span:after {
  transform: translate(18px);
}

